<template>
  <div>
    <div class="attention_item" v-for="(item, index) in result" :key="index">
      <div>
        <img :src="item.img" alt="任务ICON">
        <div class="teacher_base_info">
          <a>{{item.iteTitle}}</a><br>
          <a class="home_work" v-if="item.iteIntegral != 0">+{{item.iteIntegral}}积分</a>
          <a class="home_work" v-if="item.iteIntegral == 0">每日签到获取积分</a>
        </div>
      </div>
      <div class="note" @click="to_flow(item)" v-if="item.iteButton != '去登录'"><a>{{item.iteButton}}</a></div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        result: []
      }
    },
    methods: {
      to_flow(item){
        switch(item.iteButton){
            case '去提交':
              this.$router.push({
                name: 'workComment'
              })
              break
            case '去听课':
              this.$router.push({
                name: 'opended'
              })
              break
            case '去评论':
              this.$router.push({
                name: 'latestTime'
              })
              break
            case '去签到':
              this.$router.push({
                name: 'opended'
              })
              break
            case '去开通':
              this.$router.push({
                name: 'opended'
              })
              break
            case '去开通':
              this.$router.push({
                name: 'opended'
              })
              break
            case '去登录':
              element.iteButton = '去登录'
              break
          }
      }
    },
    mounted () {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.post('/api/getIntegralTask').then( data => {
        data.data.result.forEach(element => {
          switch(element.iteButton){
            case 'QTJ':
              element.iteButton = '去提交'
              break
            case 'QTK':
              element.iteButton = '去听课'
              break
            case 'QPL':
              element.iteButton = '去评论'
              break
            case 'QQD':
              element.iteButton = '去签到'
              break
            case 'QKTVIPKC':
              element.iteButton = '去开通'
              break
            case 'QKTSXKC':
              element.iteButton = '去开通'
              break
            case 'QKTSXKC':
              element.iteButton = '去开通'
              break
            case 'QDL':
              element.iteButton = '去登录'
              break
          }
        })
        this.result = data.data.result
        this.$Indicator.close()
      }).catch( err =>{
        console.log(err)
      })
    }
  }
</script>


<style lang="less" scoped>
  /* 关注列表 START */
  .attention_item {
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
  }
  .attention_item>div:nth-child(1) {
    -webkit-box-flex: 6;
    flex: 6;
    padding: .625rem 0 0 .625rem;
  }
  .attention_item>div:nth-child(1)>img {
    width: 1.875rem;
    float: left;
    margin-top: .625rem;
  }
  .attention_item>div:nth-child(1)>div {
    padding: .625rem 0 0 3.75rem;
  }
  .attention_item>div:nth-child(2) {
    -webkit-box-flex: 4;
    flex: 4;
  }
  .attention_item>div:nth-child(2) {
    padding-top: 1.5625rem;
  }
  .attention_item>div:nth-child(2)>a {
    border-radius: .625rem;
    padding: .625rem .9375rem;
  }
  .home_work {
    color: #aaa;
    font-size: .875rem;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 12.5rem;
    color: #ee5e1f;
  }
  .note>a {
    background-color: #0dc04a;
    color: #fff;
    border: .0625rem solid #fff;
  }
  .already_attention {
    background-color: #fff !important;
    color: #777 !important;
    border: .0625rem solid #aaa !important;
    font-size: .75rem;
    padding: .625rem .8125rem !important;
  }
  /* 关注列表 END */
</style>



